<!-- vue -->
<template>
	<div class="writeCom">
		<div class="write_content">
			<div class="article_module">
				<div class="article_title">
					<div class="title_text">:</div>
					<div class="title_input">
						<el-input
							placeholder=""
							v-model="articleData.title"
							clearable>
						</el-input>
					</div>
				</div>
				<div class="article_file">
					<div class="file_text">:</div>
					<div class="file_input">
						<el-input
							placeholder="xxx_xxx_xx,javascript_promise_01.md"
							v-model="articleData.filename"
							clearable>
						</el-input>
					</div>
				</div>
				<div class="article_upload">
					<span class="upload_title">(.txt):</span>
					<div class="upload_file">
						<el-upload
							action="#"
							accept=".txt"
							ref="upload"
							:auto-upload="false"
							:limit="1"
							:on-exceed="handleExceed"
							:on-change="beforeUpload"
							:on-remove="handleRemove"
							:file-list="articleData.fileList"
						>
							<el-button size="small" type="primary"></el-button>
							<div slot="tip" class="el-upload__tip">.txt,.md</div>
						</el-upload>
					</div>
				</div>
				<div class="article_setting">
					<el-button type="primary" @click="upload">  </el-button>
				</div>	
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'writeCom',
		components: {},
		data() {
			return {
				articleData:{
					title:'',
					filename:'',
					fileList:[]
				}
			}
		},
		computed: {

		},
		created() {

		},
		mounted() {

		},
		methods: {
			beforeUpload:function (file) {
				let that = this;
				console.log(file);
				var isTxt = file.raw.name.substring(file.raw.name.length - 4) === '.txt';
				console.log(isTxt);
				if (!isTxt) {
					this.$message.error('.txt!');
					//
					that.articleData.fileList = [];
					return;
        }
				//
				that.articleData.fileList.push(file.raw);
			},
			handleExceed:function () {
				this.$message.warning(` 1 `);
			},
			handleRemove:function () {
				let that = this;
				//
				that.articleData.fileList = [];
			},
			upload:function () {
				let that = this;
				//,
				if(!that.articleData.title || !that.articleData.filename || !that.articleData.fileList[0]){
					that.$message({
						message: '',
						type: 'error'
					});
					return;
				}
				let formdata = new FormData();
				//formmdata
				formdata.append('title',that.articleData.title);
				formdata.append('filename',that.articleData.filename);
				formdata.append('file',that.articleData.fileList[0]);
				//
				$.ajax({
					url: "/api/addArticle",
					type: "post",
					contentType:false,
					processData:false,
					data:formdata,
					success:function(res){
						console.log(res);
						const { status,data,detail } = res;
						if(status == 0){
							//datadata
							let msg = data ? data:'';
							that.$message({
								message: msg,
								type: 'error'
							});
							return;
						}
						that.$message({
							message: '',
							type: 'success'
						});
						//
						that.articleData = {
							title:'',
							filename:'',
							fileList:[]
						}
					}
				});
			}
		}

	}
</script>

<style lang="scss" scoped>
	.writeCom{
		width: 100%;
		@include articlePadding;
	  .write_content{
			width: 100%;
			.article_module{
				width: 100%;
				.article_title,
				.article_file{
					height: 50px;
					display: flex;
					align-items: center;
					font-size: 16px;
					margin: 6px 0;
				}
				.title_text,
				.file_text{
					width: 120px;
					margin-right: 10px;
				}
				.title_input,
				.file_input{
					flex: 1;
				}
				.article_upload{
					margin: 10px 0;
					.upload_file{
						padding: 10px;
					}
				}
				.article_setting{
					display: flex;
					justify-content: center;
				}
			}
		}
	}

</style>