<!-- -->
<template>
	<div class="MenuCom">
    <!--  -->
			<el-menu
				:default-active="curMenu"
				class="menuBar-main"
				@select="selectMenu">
				<template v-for="item in menuList">
					<!--  -->
					<template v-if="item.show">
						<!-- subMenu -->
						<template v-if="item.subMenu">
							<el-submenu :key="item.title" :index="item.alias">
								<template slot="title">
									<i :class="item.iconStyle.class"></i>
									<span>{{item.title}}</span>
								</template>
								<template v-for="subItem in item.subMenu">
									<el-menu-item :key="subItem.title" :index="subItem.alias">
										<span>{{subItem.title}}</span>
									</el-menu-item>
								</template>
							</el-submenu>
						</template>
						<!-- subMenu -->
						<template v-else>
							<el-menu-item :key="item.title" :index="item.alias">
								<template slot="title">
									<i :class="item.iconStyle.class"></i>
									<span>{{item.title}}</span>
								</template>
							</el-menu-item>
						</template>
					</template>
				</template>
			</el-menu>
	</div>
</template>

<script>

	export default {
		name: 'MenuCom',
		components: {},
		//
		props:{
			curMenu:String
		},
		data() {
			return {
				//
				menuList:[
					{
						//
						title:'',
						//
						alias:'Home',
						//
						iconStyle:{
							//
							path:'',
							//class
							class:'el-icon-folder'
						},
						//
						access:'1',
						//
						show:true
					},
					{
						title:'',
						alias:'Articles',
						iconStyle:{
							//
							path:'',
							//class
							class:'el-icon-reading'
						},
						access:'1',
						show:true,
						// subMenu:[
						// 	{
						// 		title:'',
						// 		alias:'FrontEnd',
						// 		iconStyle:{
						// 			//
						// 			path:'',
						// 			//class
						// 			class:''
						// 		},
						// 		access:'1',
						// 		show:true,
						// 	},
						// 	{
						// 		title:'',
						// 		alias:'BackEnd',
						// 		iconStyle:{
						// 			//
						// 			path:'',
						// 			//class
						// 			class:''
						// 		},
						// 		access:'1',
						// 		show:true,
						// 	}
						// ]
					},
					{
						title:'',
						alias:'Manage',
						iconStyle:{
							path:'',
							class:'el-icon-goods'
						},
						access:'Edit',
						show:false,
						subMenu:[
							{
								title:'',
								alias:'Write',
								iconStyle:{
									//
									path:'',
									//class
									class:''
								},
								access:'1',
								show:true,
							},
							{
								title:'',
								alias:'Edit',
								iconStyle:{
									//
									path:'',
									//class
									class:''
								},
								access:'1',
								show:true,
							}
						]
					},
				]
			}
		},
		computed: {
			//token
			getUserToken:function () {
				return this.$store.getters.getUserToken;
			}
		},
		watch: {
			//
			getUserToken:{
				handler() {
					let that = this;
					let isEdit_bool;
					//
					if(!that.getUserToken){
						//token
						isEdit_bool = false;
						//Home
						that.$store.commit('updateCurMenu','Home');
					}else{
						//token
						const { isEdit } = jwt.decode(that.getUserToken);
						isEdit_bool = Number.parseInt(isEdit) == 1;
					}
					console.log(isEdit_bool);
					for(let i in that.menuList){
						if(that.menuList[i].access === 'Edit'){
							that.menuList[i].show = isEdit_bool;
						}
					}
				},
				immediate: true
			}
		},
		created() {

		},
		mounted() {
			
		},
		methods: {
			selectMenu:function (params) {
				let vm = this;
				// console.log(params);
				//vuex
				vm.$store.commit('updateCurMenu',params);
			}
		}

	}
</script>

<style lang="scss" scoped>
	.MenuCom{
		width: 100%;
    overflow: hidden;
	}

</style>