<!-- vue -->
<template>
	<div class="editCom">
		<!--  -->
		<div class="edit_search">
			<el-select v-model="searchKey" filterable clearable placeholder="">
				<el-option
					v-for="item in articlesList"
					:key="item.articleId"
					:label="item.articleTitle"
					:value="item.articleId">
				</el-option>
			</el-select>
			<el-button size="medium" type="success" @click="doSearch"></el-button>
		</div>	
		<!--  -->
		<div class="articles_panel">
			<div class="article_data" v-for="(item,index) in articlesList" :key="index">
				<div class="article_title" >
					<template v-if="item.articleId == curActive">
						<el-input size="small" v-model="curModifyData.articleTitle" placeholder=""></el-input>
					</template>
					<template v-else>
						{{item.articleTitle}}
					</template>
					</div>
				<div class="operation">
					<template v-if="item.articleId == curActive">
						<el-upload
							class="upload_op"
							action="#"
							accept=".txt"
							ref="upload"
							:auto-upload="false"
							:limit="1"
							:on-exceed="handleExceed"
							:on-change="beforeUpload"
							:on-remove="handleRemove"
							:file-list="curModifyData.articleFile"
						>
							<el-button size="mini" type="primary"></el-button>
							<!-- <div slot="tip" class="el-upload__tip">.txt,.md</div> -->
						</el-upload>
						<el-button size="mini" type="success" @click="doSave(item.articleId,index)"></el-button>
						<el-button size="mini" type="warning" @click="doModify(item.articleId,index)"></el-button>
					</template>
					<template v-else>
						<el-button size="mini" type="primary" @click="doModify(item.articleId,index)"></el-button>
					</template>
					<el-button size="mini" type="danger" @click="doDelete(item.articleId,index)"></el-button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'editCom',
		components: {},
		data() {
			return {
				//
				articlesList: [
					{
						articleId: '1',
						articleTitle: ''
					}, {
						articleId: '2',
						articleTitle: ''
					}, {
						articleId: '3',
						articleTitle: ''
					}, {
						articleId: '4',
						articleTitle: ''
					}, {
						articleId: '5',
						articleTitle: ''
					}
				],
				//
				searchKey:'',
				//
				curActive:'',
				//
				curModifyData:{
					articleTitle:'',
					articleFile:[]
				}
			}
		},
		computed: {

		},
		created() {

		},
		mounted() {
			//
			this.init();
		},
		methods: {
			//
			init:function () {
				let that = this;
				//
				$.ajax({
					url: "/api/queryAticle",
					type: "get",
					'Content-Type':'application/x-www-form-urlencoded',
					data:{
						articleId:that.searchKey
					},
					success:function(res){
						console.log(res);
						const { status,data,detail } = res;
						if(status == 0){
							that.$message({
								message: data,
								type: 'error'
							});
							return;
						}
						that.articlesList = data;
					}
				});
			},
			//
			doSearch:function () {
				let that = this;
				console.log('',this.searchKey);
				// 
				$.ajax({
					url: "/api/queryAticle",
					type: "get",
					'Content-Type':'application/x-www-form-urlencoded',
					data:{
						articleId:that.searchKey
					},
					success:function(res){
						console.log(res);
						const { status,data,detail } = res;
						if(status == 0){
							that.$message({
								message: data,
								type: 'error'
							});
							return;
						}
						that.articlesList = data;
					}
				});
			},
			//
			doModify:function (articleId,index) {
				console.log(articleId,index);
				let that = this;
				that.curActive = articleId == that.curActive ? '' : articleId;
				if(that.curActive){
					//
					that.curModifyData.articleTitle = that.articlesList[index].articleTitle;
				}else{
					//
					that.curModifyData.articleTitle = '';
					that.curModifyData.articleFile = [];
				}
			},
			//
			doDelete:function (articleId,index) {
				let that = this;
				console.log(articleId,index);
				that.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
					//
					$.ajax({
						url: "/api/deleteArticle",
						type: "post",
						'Content-Type':'application/x-www-form-urlencoded',
						data:{
							articleId:articleId
						},
						success:function(res){
							console.log(res);
							const { status,data,detail } = res;
							if(status == 0){
								that.$message({
									message: data,
									type: 'error'
								});
								return;
							}
							that.$message({
								type: 'success',
								message: '!'
							});
							//
							that.articlesList.splice(index,1);
						}
					});
        }).catch(() => {
          that.$message({
            type: 'info',
            message: ''
          });          
        });
			},
			//
			doSave:function (articleId,index) {
				console.log(articleId,index);
				let that = this;
				//,
				if(!that.curModifyData.articleTitle){
					that.$message({
						message: '!',
						type: 'error'
					});
					return;
				}
				let formdata = new FormData();
				//formmdata
				formdata.append('articleId',articleId);
				formdata.append('title',that.curModifyData.articleTitle);
				//
				let file = that.curModifyData.articleFile.length ? that.curModifyData.articleFile[0] : '';
				formdata.append('file',file);
				// 
				$.ajax({
					url: "/api/updataArticle",
					type: "post",
					contentType:false,
					processData:false,
					data:formdata,
					success:function(res){
						console.log(res);
						const { status,data,detail } = res;
						if(status == 0){
							//datadata
							let msg = data ? data:'';
							that.$message({
								message: msg,
								type: 'error'
							});
							return;
						}
						that.$message({
							message: data,
							type: 'success'
						});
						//
						that.articlesList[index].articleTitle = that.curModifyData.articleTitle;
						//
						that.curActive = '';
						that.curModifyData = {
							articleTitle:'',
							articleFile:[]
						}
					}
				});
			},
			handleExceed:function () {
				this.$message.warning(` 1 `);
			},
			beforeUpload:function (file) {
				let that = this;
				console.log(file);
				var isTxt = file.raw.name.substring(file.raw.name.length - 4) === '.txt';
				console.log(isTxt);
				if (!isTxt) {
					this.$message.error('.txt!');
					//
					that.curModifyData.articleFile = [];
					return;
        }
				//
				that.curModifyData.articleFile.push(file.raw);
				
			},
			handleRemove:function () {
				//
				this.curModifyData.articleFile = [];
			}
		}

	}
</script>

<style lang="scss" scoped>
	.editCom{
		width: 100%;
		@include articlePadding;
		.edit_search{
			width: 100%;
			padding: 10px;
			.el-select{
				width: calc(100% - 100px);
    		margin-right: 20px;
			}
		}
		.articles_panel{
			padding: 10px;
			.article_data{
				display: flex;
				justify-content: space-between;
				align-items: center;
				.article_title{
					padding: 6px 0;
				}
				.operation{
					display: flex;
					align-items: center;
					.upload_op{
						display: inline-block;
						margin-right: 10px;
					}
				}
			}
		}
	}

</style>